@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700;800;900&display=swap);
* {
    font-family: 'Outfit', sans-serif;
}

.github-profile {
    display: flex;
    align-items: center;
	margin: 1rem;
    border-bottom: solid 1px rgb(75, 75, 75);
    padding-bottom: 1rem;
}

img {
    width: 75px;
    border-radius: 15px;
}

.info {
    display: flex;
    flex-direction: column;
    margin-left: 12px;
}

.name {
    font-size: 1.25rem;
  font-weight: bold;
}

.align {
    margin: 3px 0px;
}
form {
    border: thin solid #888;
  padding: 2rem;
  margin-bottom: 2rem;
  display: flex;
  justify-content: center;
  background-color: #eee;
  border: none;
}

input[type=text] {
    padding: 5px;
    font-size: 1.3rem;
    font-family: 'Outfit', sans-serif;
    font-weight: 400;
    border: none;
    border-bottom: 2px solid rgba(0, 22, 117, .5);
    margin: 10px;
    outline: none;
    background-color: transparent;
}

button {
    font-size: 1.3rem;
    font-family: 'Outfit', sans-serif;
    border-radius: 10px;
    border: none;
    background-color: rgb(0, 22, 117);
    color: white;
    padding: 15px;
    cursor: pointer;
}

@media only screen and (max-width: 600px) {
    form {
        flex-direction: column;
    }

    input[type=text] {
        text-align: center;
    }
}
.app {
    display: flex;
    flex-direction: column;
}

.header {
	text-align: center;
  font-size: 1.5rem;
  margin: 2rem;
  font-weight: 700;
  font-family: 'Outfit', sans-serif;
}

.dev {
    font-size: 0.9rem;
    font-family: 'Outfit', sans-serif;
    text-decoration: none;
    text-align: right;
    color: rgb(3, 3, 41);
}

.dev:hover {
    text-decoration: underline;
    color: blue;
}
